@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@500&display=swap&family=Josefin+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500;600;700&display=swap);
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}


:root {
  --primary-blue-100: #0088cc;
  --primary-green: #cedb00;
  --primary-blue-bg: rgba(0, 136, 204, 0.8);
  --primary-brown: rgb(204, 113, 0);
  --primary-blue-200: #005580;
  --primary-grey-bg: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0.8),
    rgba(200, 200, 200, 0.2)
  );
}


#journey,#home,#portfolio,#contact{
  margin-top: 0px;
  padding-top: 65px;

}

#journey {
  width: 100vw;
  min-height: 100vh;
  padding-bottom: 50px;
}

#home {
  padding-top: 85px;
  width: 100vw;
  padding-bottom: 65px;
}

#portfolio {
  width: 100vw;
  min-height: 100vh;
  background: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0.8),
    rgba(200, 200, 200, 0.2)
  );
  background: var(--primary-grey-bg);
}


#about {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0.8),
    rgba(200, 200, 200, 0.2)
  );
  background: var(--primary-grey-bg)
}

span[class="carousel-control-next-icon"], span[class="carousel-control-prev-icon"]{
  visibility: hidden;
  background-color: transparent !important;
  color:transparent !important;
}

ol[class="carousel-indicators"]{
  display: none;
}
.aboutPicture {
  width: 300px;
  height: 300px;
  border-radius: 50%;
}

.sectionCaption {
  text-align: center ;
  color: var(--primary-brown);
  font-size: 56px;
  font-family: "Josefin Sans", sans-serif;
  letter-spacing: 0px ;
}

.aboutContent {
  text-align: left;
  color: rgba(10, 10, 10, 0.8);
  font-size: 20px;
  line-height: 35px;
  font-family: "Roboto";
}

.aboutContent > .strong-text {
  text-align: center;
  margin: 10px 0px;
}

.icon-grid{
  display: grid;
  grid-template-columns: repeat(5, 70px);
  grid-template-rows: minmax(64px, 70px);
  grid-column-gap: 80px;
  -webkit-column-gap: 80px;
          column-gap: 80px;
  grid-row-gap: 15px;
  row-gap: 15px;
  justify-content: center
}

.icon > img {
  height: 64px;
  width: 64px;
}

.profilePicture {
  width: 300px !important;
  height: 300px !important;
  border-radius: 50% !important;
}

.displayName1, .displayName2 {
  float: left;
  font-size: 45px;
  background: linear-gradient(
    to top left,var(--primary-brown), rgba(204,113,0,0.8)
  );
  margin: 0px 5px;
  font-weight: 600;
  font-family: 'Josefin Sans', sans-serif;
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

.confetti {
  z-index: 2;
}

.typed-cursor {
  display: none;
}

.displayImage {
  height: 225px;
  width: 275px;
  border-radius: 15%;
  object-fit: cover;
}

.sectionCaptionJourney {
  margin-top: 34px;
  text-align: center;
  color: var(--primary-brown);
  font-size: 56px;
  font-family: "Josefin Sans", sans-serif;
  letter-spacing: 0px;
}

.contentContainer {
  position: relative;
  margin-top: 65px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
  grid-row-gap: 40px;
  row-gap: 40px;
}

.left {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  transform: translateX(100px);
  grid-column: 1/2;
  padding-right: 40px;
}

.right {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
  grid-column: 2/3;
}

.contentContainer .imageWrapper:not(:nth-last-child(2)):after {
  content:"";
  position: absolute;
  height: 100%;
  top: 63%;
  right: 515px;
  border-left: 1px dashed #444; 
}



.imageWrapper::before {
  position: absolute;
  right: 500px;
  width: 30px;
  height: 30px;
  border: 1px solid grey;
  border-radius: 50%;
  content: "";
}


.imageWrapper {
  position: relative;
}

.institution{
  color: var(--primary-blue-200);
}

.natureOfWork{
  color: green;
}

#navbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: auto;
  width: 100% 
}

.nav-item {
  margin-right: 0.625rem !important;
  margin-left: 0.625rem !important;
}

.nav-link {
  border-top: 5px solid rgba(0, 0, 0, 0);
  color: var(--primary-blue-100) !important;
}

.nav-link:hover {
  cursor: pointer;
  border-top: 3px solid orange;
}

/* .carousel {
  width: 25% !important;
} */
.carouselWrapper{
  margin-top: 35px;
  width: 450px; 
  margin-left: auto;
  margin-right: auto;
  height: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sectionCaptionPortfolio {
  margin-top: 34px;
  text-align: center;
  color: var(--primary-brown);
  font-size: 56px;
  font-family: "Josefin Sans", sans-serif;
  letter-spacing: 0px;
}

.carousel-card{
  width: 450px !important;
  border-radius: 15px !important;
  overflow: hidden !important;
}

.carouselImage{
  display: inline-block !important;
  text-align: center !important;
}
.cardTitle{
  color:var(--primary-blue-200);
}

.cardText{
  color: rgba(0,0,0,0.8);
}

.card-image-container{
  overflow: hidden;
}



